import noData from '@/components/noData'
import back from '@/components/back'
import { introAdmin } from '../../config'

export default {
	components: {
		noData,
		back
	},
	data() {
		return {
			user: this.$store.state.userInfo,
			page: 1,
			haveMore: true,
			defaultImg: 'this.src="' + require('../../assets/prefRecommend/1.jpg') + '"',
			ListDetails: this.$store.state.preList,
			search: '',
			activeTab: 0,
			list: [],
			list2: [],
			pageShow: false,
			istHos: false,
			num: null,
			fromIM: this.$route.query.fromIM ? true : false,
			showConfirm: false,
			thisId: null,
			showCreate:false,
			introAdmin:introAdmin
		}
	},
	created() {
		this.pull()
		this.istHosGroupMember()
	},
	mounted() {
		this.getMore()
	},
	beforeDestroy() {
		window.onscroll = null
		this.chooseItem = null
	},
	methods: {
		istHosGroupMember() {
			this.$get('PlatFormAPI/SpecialistHosGroup/IsSpecialistHosGroupMember', this.$pick({
				DrId: this.user.Data.User.DoctorId,
			})).then(o => {
				if(o) {
					this.istHos = true
				}
			})
		},
		activeTabT(i) {
			this.page = 1
			this.haveMore = true
			this.pageShow = false
			this.search = ''
			if(i == 1) {
				this.activeTab = 1
				this.pull2()
			} else {
				this.activeTab = 0
				this.pull()
			}
			this.num = i
		},
		searchFor() {
			this.page = 1
			this.haveMore = true
			this.pageShow = false
			if(this.num == 1) {
				this.pull2()
			} else {
				this.pull()
			}
		},
		pull(h) {
			if(this.haveMore) {
				this.$post('PlatFormAPI/SpecialistHosGroup/GetGoodsRecommendUsed', this.$pick({
					DrId: this.user.Data.User.DoctorId,
					SearchContent: this.search,
					PageIndex: this.page,
					PageSize: 10
				})).then(o => {
					this.pageShow = true
					if(!o.ReturnData || o.ReturnData.length < 10) {
						this.haveMore = false
					} else {
						this.haveMore = true
					}

					if (h){
					    o.ReturnData.forEach(v => {
					        this.list.push(v)
					    })
					}
					else {
					    this.list = o.ReturnData
					}
					
					/*let _this = this
					o.ReturnData = o.ReturnData || []
					o.ReturnData.forEach((v, k) => {
						let isCreatBag = false
						v.ListDetails.some(function(i) {
							//商品类型(1-平台服务，2-专科商品，3-平台商品，4-医生自定义，5-VIP预约)
							if(i.GoodsSource == 3 || (i.GoodsSource == 1 && i.DrId == _this.user.Data.User.DoctorId) || (i.GoodsSource == 4 && i.DrId == _this.user.Data.User.DoctorId)) {
								isCreatBag = true
								return true;
							}
						})
						v = {
							...v,
							isCreatBag: isCreatBag
						}
						_this.list.push(v)
					})*/

				})
			}
		},
		pull2(h) {
			if(this.haveMore) {
				this.$post('PlatFormAPI/SpecialistHosGroup/GetGoodsRecommendHistory', this.$pick({
					DrId: this.user.Data.User.DoctorId,
					SearchContent: this.search,
					PageIndex: this.page,
					PageSize: 10
				})).then(o => {
					this.pageShow = true
					if(!o.ReturnData || o.length < 10) {
						this.haveMore = false
					} else {
						this.haveMore = true
					}

					if(h) {
						o.ReturnData.forEach(v => {
							this.list2.push(v)
						})
					} else {
						this.list2 = o.ReturnData
					}
					
					/*let _this = this
					o.ReturnData = o.ReturnData || []
					o.ReturnData.forEach((v, k) => {
						let isCreatBag = false
						v.ListDetails.some(function(i) {
							if(i.GoodsSource == 3 || (i.GoodsSource == 1 && i.DrId == _this.user.Data.User.DoctorId) || (i.GoodsSource == 4 && i.DrId == _this.user.Data.User.DoctorId)) {
								isCreatBag = true
								return true;
							}
						})
						v = {
							...v,
							isCreatBag: isCreatBag
						}
						_this.list2.push(v)
					})*/
				})
			}
		},
		preDetail(b) {
			this.$store.dispatch('pushrecomMsg', {
				DataSource: 2, //数据来源(1：常用推荐，2：历史推荐，3：新增推荐)
				DataId: b.GoodsRecomId, //数据来源Id
				fromIM: this.fromIM
			})
			this.$store.dispatch('pushpreList', [])
			this.$store.dispatch('pushgoodList', null)
			this.$store.dispatch('pushpreId', 0)
			this.$router.push(`/prefRecommend/reDetail`)
		},
		preDetailC(a) {
			this.$store.dispatch('pushrecomMsg', {
				DataSource: 1, //数据来源(1：常用推荐，2：历史推荐，3：新增推荐)
				DataId: a.GoodsRecomUsedId, //数据来源Id
				fromIM: this.fromIM
			})
			this.$store.dispatch('pushpreList', [])
			this.$store.dispatch('pushgoodList', null)
			this.$store.dispatch('pushpreId', 0)
			this.$router.push(`/prefRecommend/reDetail`)
		},
		creatBag(a) {
			//生成服务包
			let details = a.ListDetails
			let arr = [] //商品的列表
			let arr2 = [] //医生的平台服务
			let arr3 = [] //医生的自定义服务
			let isCreatBag = false
			details.forEach((v, k) => {
				//商品类型(1-平台服务，2-专科商品，3-平台商品，4-医生自定义，5-VIP预约)
				if((v.GoodsSource == 1 || v.GoodsSource == 4) && (v.DrId != this.user.Data.User.DoctorId)) {
					isCreatBag = true
				}
				//商品
				if(v.GoodsSource == 3) {
					v.ServiceItemId = v.GoodsId
					v.ServiceItemName = v.GoodsName
					v.Price = v.OnePrice
					v.VipPrice = v.OnePrice
					v.MasterPicture = v.ImgPath
					v.NumValue = v.GoodsNum
					v.isChecked = 1
					arr.push(v)
				}
				//医生的平台服务
				if(v.GoodsSource == 1 && v.DrId == this.user.Data.User.DoctorId) {
					v.ServiceItemId = v.GoodsId
					v.ServiceItemName = v.GoodsName
					v.SericeItemCode = v.GoodsNo
					v.EachPrice = v.OnePrice
					v.MasterPicture = v.ImgPath
					v.NumValue2 = v.GoodsNum
					v.isChecked = 1
					arr2.push(v)
				}
				//医生的自定义服务
				if(v.GoodsSource == 4 && v.DrId == this.user.Data.User.DoctorId) {
					v.ServiceItemId = v.GoodsId
					v.ServiceItemName = v.GoodsName
					v.SericeItemCode = v.GoodsNo
					v.Price = v.OnePrice
					v.MasterPicture = v.ImgPath
					v.NumValue = v.GoodsNum
					v.isChecked = 1
					v.Description = ""
					v.NumTypeName = "次"
					arr3.push(v)
				}
			})
			this.$store.dispatch('pushpreList', arr)
			this.$store.dispatch('pushservice', {
				Details: arr2
			})
			this.$store.dispatch('pushcustomSevice', arr3)
			this.$store.dispatch('pushserviceBase', null)
			if(isCreatBag){
				this.showCreate = true
			}else{
				this.$router.push("/create-service-base?fromPref=1")
			}
		},
		confirmCtreat(){
			//生成服务包
			this.$router.push("/create-service-base?fromPref=1")
		},
		cancleCtreat(){
			this.$store.dispatch('pushpreList', [])
			this.$store.dispatch('pushservice', null)
			this.$store.dispatch('pushcustomSevice', [])
			this.$store.dispatch('pushserviceBase', null)
			this.showCreate = false
		},
		allGoods() {
			this.$store.dispatch('pushrecomMsg', {
				DataSource: 3, //数据来源(1：常用推荐，2：历史推荐，3：新增推荐)
				DataId: 0, //数据来源Id
				fromIM: this.fromIM
			})
			if(this.istHos) {
				this.$store.dispatch('pushpreList', [])
				this.$store.dispatch('pushgoodList', null)
				this.$store.dispatch('pushpreId', 0)
				this.$store.dispatch('pushscrollPo', null)
				this.$router.push(`/prefRecommend/preSpecialty`)
			} else {
				this.$store.dispatch('pushpreList', [])
				this.$store.dispatch('pusharea', null)
				this.$store.dispatch('pushallSpa', null)
				this.$store.dispatch('pushgoodList', null)
				this.$store.dispatch('pushpreId', 0)
				this.$router.push(`/prefRecommend/preAllSpecialty`)
			}
		},
		newJian() {
			this.$store.dispatch('pushrecomMsg', {
				DataSource: 1, //数据来源(1：常用推荐，2：历史推荐，3：新增推荐)
				DataId: 0, //数据来源Id
				fromIM: this.fromIM
			})
			this.$store.dispatch('pushpreList', [])
			this.$store.dispatch('pushgoodList', null)
			this.$store.dispatch('pushpreId', 0)
			this.$store.dispatch('pushscrollPo', null)
			if(this.istHos) {
				this.$router.push(`/prefRecommend/preSpecialty`)
			} else {
				this.$router.push(`/prefRecommend/preAllSpecialty`)
			}
		},
		del(i, idx) {
			this.showConfirm = true
			this.thisId = i.GoodsRecomUsedId
		},
		confirm() {
			this.$post('PlatFormAPI/SpecialistHosGroup/DelGoodsRecommendUsed', {
				id: this.thisId
			}).then(o => {
				this.$toast('已删除')
				this.page = 1
				this.haveMore = true
				this.pageShow = false
				this.pull()
			})
		},

		getMore() {
			const el = this.$refs.el
			let timer = null
			window.onscroll = () => {
				clearTimeout(timer)
				timer = setTimeout(() => {
					const top = document.documentElement.scrollTop ||
						window.pageYOffset ||
						document.body.scrollTop,
						h = el.scrollHeight,
						wh = window.innerHeight
					if(h - top - wh < 10 && this.haveMore) {
						this.page += 1
						if(this.num == 1) {
							this.pull2(true)
						} else {
							this.pull(true)
						}
					}
				}, 100)
			}
		},
	}
}



